
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
header{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100px;
  width: auto;
  z-index: 3;
}
.layer{
  position: relative;
  background-color: rgba(253, 156, 181);
img {
        max-width: 100%;
        height: auto;
        display: block;
        margin: 0 auto;
}
}
.context{
  position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    background-color: rgba(174, 0, 255, 0.7);
div {
        width: 100%;
        margin: 0 auto;
        font-size: 1rem;
        line-height: 1.7;
        padding: 20px 0;
}
}

/* SP向けの設定 */
@media screen and (max-width: 600px) {
.layer-txt{
   font-size: 1.4rem;
   padding: 4rem 0;
}
}

/* PC向けの設定 */
@media screen and (min-width: 601px) {
.layer-txt{
    font-size: 2rem;
    padding: 8rem 0;
}
}

